import React from 'react';
import { Link, NavLink } from "react-router-dom";
import SOCIALS from './data/socials';

class Social extends React.Component{


    render(){

        const {name, url, image, image_small, internalPage} = this.props.social
        let curPath = window.location.pathname;
        let trailingPathExist = (
            (curPath.slice(-1) === "/")
            ? true
            : false
        );

        if (trailingPathExist){
            curPath = curPath.slice(0, -1);
        }

        return (
            <li>    
                {
                    (internalPage)
                    ? (
                        <>
                            {
                                (curPath.trim().toLowerCase() === url.trim().toLowerCase())
                                    //same page
                                    ?(
                                        <NavLink to="#" onClick={
                                            function(e){
                                                e.preventDefault();
                                                window.scrollTo({ 
                                                    top:0,
                                                    left: 0, 
                                                    behavior: 'smooth'
                                                });
                                            }
                                        }>
                                            <img
                                                src={(this.props.currentBreakpoint <= 960) ? image_small : image}
                                                alt={name}
                                            />
                                        </NavLink>
                                    )

                                    //Linking to internal pages
                                    : (<Link to={{ pathname: url }}>
                                        <img
                                            src={(this.props.currentBreakpoint <= 960) ? image_small : image}
                                            alt={name}
                                        />
                                    </Link>
                                    )
                            }
                        </>
                    )
                   // this is a linkout/email, hence dont use LINK tag
                    : (
                        <a href={url} 
                            target={(url.toLowerCase().indexOf('mailto', 0) >= 0) ? null :"_blank"} 
                                rel={(url.toLowerCase().indexOf('mailto', 0) >= 0) ? null : "noopener noreferrer"} >
                            <img src={image} alt={name}/>
                        </a>
                    )
                }
                
            </li>
        )
    }
}

class Socials extends React.Component{

    
    render(){
        return (
            <div className="socials">
                <ul>
                    {
                        SOCIALS.map(SOCIAL => {
                            return (
                                <Social key={SOCIAL.id} social={SOCIAL} currentBreakpoint={this.props.currentBreakpoint}  />
                            )
                        })
                    }
                </ul>
            </div>
        )
    }
}

export default Socials;